import React, { useContext, useEffect } from "react";
import PostList from "./PostList";
import NewPostButton from "./common/NewPostButton";
import { useFetchTrashedPosts } from "../lib/hooks";

import PostsContext from "../PostsContext";

export default function TrashedPosts() {
	const {
		posts: { trashed },
		postsDispatch,
	} = useContext(PostsContext);

	useEffect(() => {
		postsDispatch({
			type: "REFETCH",
		});
	}, [postsDispatch]);

	const isLoading = useFetchTrashedPosts();

	return (
		<>
			<PostList
				className="trashedPosts"
				date={false}
				posts={trashed}
				allowDrag={true}
				loadingState={isLoading}
			/>
			<NewPostButton unscheduled={true} />
		</>
	);
}
